

export default function BlockButton({children,onClick}) {
  const handleClick=()=>{
    if(onClick){
      onClick()
    }
  }
    return (
        <div onClick={()=>handleClick()} className=' hover:bg-onBackWarring  cursor-pointer h-[40px] pl-[20px] pr-[20px]  rounded-[20px] bg-cblock   flex items-center '>
          {children}
        </div>
    )
}